<template>
  <div class="transaction-info">
    <p>
      <strong>{{ $t('Customer') }}:</strong> {{ tx.customer.firstName }} {{ tx.customer.lastName }}<br>
      <strong>{{ $t('Customer No') }}:</strong> {{ tx.customer.customerNumber }}<br>
      <strong>{{ $t('Credit Account No') }}:</strong> {{ tx.creditAccountNumber }}<br>
      <strong>{{ $t('Transaction Id') }}:</strong> {{ tx.transactionId }}<br>
      <strong>{{ $t('Order Amount') }}:</strong> {{ orderAmount }}<br>
    </p>

    <div class="progress-bar">
      <div
        v-for="(field, index) in progressFields"
        :key="field.label"
        class="progress"
      >
        <strong>{{ field.label }}</strong><br>
        <span>{{ field.value ? field.value : 'n/a' }}</span><br>

        <span v-if="index != Object.keys(progressFields).length - 1">|</span>
      </div>
    </div>
  </div>
</template>

<script>
import i18n from '../../de_DE.js'
import helpers from '../../helpers.js'

export default {
  props: {
    tx: Object
  },
  computed: {
    progressFields () {
      return [{
        label: this.$t('Order'),
        value: helpers.formatDate(this.tx.orderDetails.orderDate)
      }, {
        label: this.$t('Shipping'),
        value: helpers.formatDate(this.tx.bookings.find(b => b.type === 'CAPTURE')?.created)
      }, {
        label: this.$t('Clearing'),
        value: helpers.formatDate(this.tx.bookings.find(b => b.type === 'NOTIFY')?.created)
      }, {
        label: this.$t('Refund'),
        value: helpers.formatDate(this.tx.bookings.find(b => b.type === 'REFUND')?.created)
      }]
    },
    orderAmount () {
      return helpers.formatCurrency(this.tx.orderDetails.currentOrderValue)
      + ' / ' + helpers.formatCurrency(this.tx.orderDetails.originalOrderValue)
    }
  },
  methods: {
    $t (key) {
      return i18n.get(key)
    }
  }
}
</script>
